    constructor(private http: HttpClient) { 
        this.histroyUrl = BasePathService.convertPath(this.histroyUrl)
        this.url = BasePathService.convertPath(this.url)
    }
    private histroyUrl: string = "/api/runtime/sys/v1.0/querysolution/previousQuery";
    url: string = '/api/runtime/sys/v1.0/querysolution';
    /**
     * 
     * @param querySolution 
     */
    create(querySolution: QuerySolutionVo, sessionId: string): Observable<any> {
        let headers = new HttpHeaders({
            'Content-Type': 'application/json',
            'sessionid': sessionId
        });
        const options = {
            headers: headers,
            sessionid: sessionId
        };
        let convertor = new QuerySolutionVoConvertor();
        let body = convertor.convertJObject(querySolution);
        return this.http.post(this.url, body, options);
    }

    /**
     * ID
     * @param formId ID
     */
    query(formId: string, sessionId: string): Observable<any> {
        let headers = new HttpHeaders({
            'Content-Type': 'application/json',
            'sessionid': sessionId
        });
        const options = {
            headers: headers,
        };
        let belongId = formId;
        // let url = `http://localhost:5100/api/dev/main/v1.0/querysolution/bybelongid/${belongId}`;
        let url = `${this.url}/belongId/${belongId}`;
        return this.http.get(url, options);
    }

