    /**
        * 
        */
    supportLS() {
        if(window.hasOwnProperty("localStorage")) {
            return true;
        }
        return false;
    }
    /**
     * 
     * @param key 
     * @param value 
     */
    setLS(key, value: any) {
        let conditionsStr = '[]';
        if(value) {
            if( typeof value == 'string') {
                conditionsStr = value;
            } else {
                const convertor = new QuerySolutionVoConvertor();
                conditionsStr = JSON.stringify(convertor.formatConvertJObject(value));
            }
           
        }
        if(key && this.supportLS) {
            localStorage.setItem(key, conditionsStr);
        } 
    }
    /**
     * 
     * @param key 
     */
    getLS(key): Array<QueryCondition> {
        let result = new Array<QueryCondition>();
        if(key && this.supportLS) {
            var v = localStorage.getItem(key);
            if (v != undefined || v !== null) {
                result = JSON.parse(v);
            }
        }
        if(result.length > 0) {
            const convertor = new QuerySolutionVoConvertor();
            return convertor.formatFromJobject(result);
        }
        return result;
    }

    /**
     * 
     * @param querySolution 
     */
    saveHistroy(querySolution: QuerySolutionVo, sessionId: string) {
        let headers = new HttpHeaders({
            "Content-Type": "application/json",
            sessionid: sessionId
        });
        const options = {
            headers: headers
        };
        let convertor = new QuerySolutionVoConvertor();
        let body = convertor.convertJObject(querySolution);
        return this.http.put(this.histroyUrl, body, options);
    }

